<template>
  <div>
    <el-carousel
      class="h-screen"
      :autoplay="false"
      indicator-position="none"
      ref="swiper"
      arrow="never"
    >
      <el-carousel-item
        v-for="item in tabs"
        :key="item.url"
        class="w-screen h-screen"
        :style="{
          background:
            'linear-gradient(rgba(0,0,0,0.8), rgba(0,0,0,0.8)),url(' +
            item.url +
            ')',
          backgroundSize: '100% 100%',
        }"
      >
        <component :is="item.name"/>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
import Screen1 from "./components/screen1.vue";
// import Screen2 from "./components/screen2.vue";
// import Screen3 from "./components/screen3.vue";
export default {
  components: {
    Screen1,
    // Screen2,
    // Screen3
  },
  data() {
    return {
      touchX:0,
      tabs: [
        {
          url: require("@/assets/images/流风遗话/screen1/bg.png"),
          name:"Screen1",
        },
        // {
        //   url: require("@/assets/images/流风遗话/screen2/bg.png"),
        //   name:"Screen2",
        // },
        // {
        //   url: require("@/assets/images/流风遗话/screen3/bg.png"),
        //   name:"Screen3",
        // },
      ],
    };
  },
  mounted(){
    this.$refs.swiper.$el.addEventListener('touchstart',(e)=>{
      this.touchStart(e)
    })
    this.$refs.swiper.$el.addEventListener('touchend',(e)=>{
      this.touchEnd(e)
    })
  },
  methods:{
    touchStart(e){
      this.touchX = e.touches[0].pageX
    },
    touchEnd(e){
      console.log(e.changedTouches[0].pageX,this.touchX);
      if(e.changedTouches[0].pageX>this.touchX){
        this.$refs.swiper.prev()
      }else if(e.changedTouches[0].pageX<this.touchX){
        this.$refs.swiper.next()
      }
    }
  }
};
</script>

